tó una serie de principios rectores que orientan toda la actuación procesal y que deben ser observados por los jueces, las partes y demás intervinientes en cada etapa del proceso. Estos principios no son meras directrices teóricas, sino que tienen fuerza normativa y su desconocimiento puede acarrear nulidades o sanciones disciplinarias, además de afectar el derecho al debido proceso (Correa Medina, 2018).")]),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Estudiemos algunos principios:")]),_c('div',{staticClass:"row align-items-start mb-5",attrs:{"data-aos":"fade-left"}},[_vm._m(11),_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Oralidad"}},[_c('p',[_vm._v("A diferencia del sistema anterior, basado principalmente en documentos escritos y trámites extensos, el nuevo modelo procesal privilegia el uso de la palabra hablada como mecanismo para dinamizar las audiencias y reducir la duración del proceso. La oralidad no significa la exclusión de lo escrito, sino que la esencia del debate probatorio y argumentativo, debe concentrarse en las audiencias orales, donde el juez puede apreciar directamente la actuación de las partes y valorar, con mayor certeza, la prueba. Por ejemplo, en una audiencia inicial dentro de un proceso de restitución de inmueble arrendado, las partes deben presentar sus pretensiones, excepciones, acuerdos probatorios y solicitud de pruebas en presencia del juez, quien tomará decisiones inmediatas sobre su admisibilidad, evitando dilaciones innecesarias (Hernández Velasco, 2015).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Inmediación"}},[_c('p',[_vm._v("Este implica que el juez debe estar presente en las audiencias más importantes del proceso, especialmente en aquellas donde se practican pruebas. Esto garantiza que el juez tenga un conocimiento directo de los testimonios, los documentos y las actuaciones, lo que refuerza la imparcialidad y la calidad del juicio. Por ejemplo, si en una audiencia de instrucción se va a escuchar al testigo principal de un proceso por incumplimiento contractual, es el juez quien debe interrogarlo y observar su comportamiento, sin delegar esta función a un auxiliar. Así, la inmediación fortalece la convicción judicial sobre los hechos debatidos.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Concentración"}},[_c('p',[_vm._v("Busca que las actuaciones procesales se realicen en el menor número de diligencias posibles, evitando la fragmentación del proceso y favoreciendo decisiones oportunas. Bajo este principio, la audiencia inicial, la audiencia de instrucción y la audiencia de juicio se desarrollan sin largas interrupciones, y las decisiones del juez se toman dentro del mismo acto o en plazos breves. Por ejemplo, si en la audiencia inicial no se logra una conciliación, el juez debe resolver inmediatamente sobre las pruebas pedidas, fijar los hechos objeto de debate y programar la audiencia de instrucción. Esto permite que el proceso avance sin dilaciones que perjudiquen a las partes.")])])])],1)]),_c('div',{staticClass:"row align-items-start mb-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-8 mb-3 mb-lg-0"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Publicidad"}},[_c('p',[_vm._v("Garantiza que las actuaciones procesales sean accesibles a las partes y, en la mayoría de los casos, también al público en general. Este principio permite que cualquier interesado pueda asistir a las audiencias o consultar los expedientes, salvo que existan razones legales para limitar el acceso, como en procesos de familia o casos que comprometan la intimidad personal. Por ejemplo, en un proceso de responsabilidad médica, las audiencias serán públicas, salvo que se deban tratar temas confidenciales como historiales clínicos. La publicidad protege la transparencia judicial y refuerza la confianza ciudadana en el sistema de justicia.")])]),_c('div',{staticClass:"div",at